body {
	background:#ccc {} }

.main {
	display: block;
	width: 600px;
	margin: 0 auto; }

h3 {
	background: #fff;
	color: #3498db;
	font-size: 36px;
	line-height: 100px;
	margin: 10px;
	padding: 2%;
	position: relative;
	text-align: center; }

.action {
	display: block;
	margin: 100px auto;
	width: 100%;
	text-align: center; }

.action a {
	display: inline-block;
	padding: 5px 10px;
	background: #f30;
	color: #fff;
	text-decoration: none; }

.action a:hover {
	background: #000; }

.image-link {
	cursor: zoom-in; }


.mfp-title {
	text-align: center;
	padding: 6px 0; }
